import React from 'react'
import { HashRouter as Router, Link, Switch, Route, useRouteMatch, useLocation, useParams } from 'react-router-dom'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Media from 'react-bootstrap/Media'
import { Card, CardDeck, ListGroup, Container } from 'react-bootstrap/'
import './Router.css'
import image1 from '../Steve_Jobs.jpg'
import image2 from '../mark.png'
import image3 from '../sundar.jpg'
import developer from '../developer.png'
import logo from '../snakedev.png'
import vision from '../our-vision.png'

function Home() {
    return (
        <div>
            <img className='img-fluid' src={developer} alt='Developer' width='100%'></img>
        </div>
    )
}
function Blogs() {
    return (
        <Container>
            <ul className="list-unstyled mt-5 p-3">
                <Media as="li">
                    <img width={64} height={64} className="mr-3" src={image1} alt="Generic placeholder" />
                    <Media.Body>
                        <h5>Steve Paul Jobs</h5>
                        <p>
                            Steven Paul Jobs (Feb 24, 1955 – October 5, 2011) was an American business magnate,
                            industrial designer, investor, and media proprietor. He was the chairman, chief executive
                            officer (CEO), and co-founder of Apple Inc., the chairman and majority shareholder of
                            Pixar, a member of The Walt Disney Company's board of directors following its
                            acquisition of Pixar, and the founder, chairman, and CEO of NeXT.
                        </p>
                    </Media.Body>
                </Media>
                <Media as="li">
                    <img width={64} height={64} className="mr-3" src={image2} alt="Generic placeholder" />
                    <Media.Body>
                        <h5>Mark Elliot Zuckerberg</h5>
                        <p>
                            Mark Elliot Zuckerberg (born May 14, 1984) is an American media magnate, internet
                            entrepreneur, and philanthropist. He is known for co-founding Facebook, Inc. and
                            serves as its chairman, chief executive officer, and controlling shareholder.
                            He also is a co-founder of the solar sail spacecraft development project Breakthrough
                            Starshot and serves as one of its board members.
                        </p>
                    </Media.Body>
                </Media>
                <Media as="li">
                    <img width={64} height={64} className="mr-3" src={image3} alt="Generic placeholder" />
                    <Media.Body>
                        <h5>Sundar Pichai</h5>
                        <p>
                            Pichai Sundararajan (born June 10, 1972), also known as Sundar Pichai, is an Indian-American
                            business executive, the chief executive officer (CEO) of Alphabet Inc. and its subsidiary
                            Google LLC. Pichai began his career as a materials engineer and joined Google as a management
                            executive in 2004. He rose to become the company's Product Chief and the CEO in 2015, as
                            part of the restructuring process that turned Alphabet Inc. into Google's parent company.
                            In December 2019, he additionally became CEO of Alphabet Inc.
                        </p>
                    </Media.Body>
                </Media>
            </ul>
        </Container>
    )
}
function Gallary() {
    return (
        <CardDeck className='w-100 '>
            <Card>
                <Card.Img variant="top" src={image1} width={150}
                    height={500} />
            </Card>
            <Card>
                <Card.Img variant="top" src={image2} width={150}
                    height={500} />
            </Card>
            <Card>
                <Card.Img variant="top" src={image3} width={150}
                    height={500} />
            </Card>
        </CardDeck>
    )
}
function Services() {
    return (
        <div className='p-5 d-flex text-center justify-content-center my-3'>
            <Card className='mr-5' border='warning' style={{ width: '18rem' }}>
                <ListGroup variant="flush">
                    <ListGroup.Item>  Ecommerce Development</ListGroup.Item>
                    <ListGroup.Item>Mobile App Development</ListGroup.Item>
                    <ListGroup.Item>Digital Marketing Services</ListGroup.Item>
                    <ListGroup.Item>Software Consulting Services</ListGroup.Item>
                    <ListGroup.Item>Software Testing Services</ListGroup.Item>
                </ListGroup>
            </Card>
            <Card className='' border='warning' style={{ width: '18rem' }}>
                <ListGroup variant="flush">
                    <ListGroup.Item> Website Development</ListGroup.Item>
                    <ListGroup.Item>Software Development Services</ListGroup.Item>
                    <ListGroup.Item>Software Maintenance Services</ListGroup.Item>
                    <ListGroup.Item>Internet of things</ListGroup.Item>
                    <ListGroup.Item>Dedicated Development Center</ListGroup.Item>
                </ListGroup>
            </Card>
        </div>
    )
}
function ContactUs() {
    return (
        <div className='bgImage '>
            <div className='content'>
                <h2> Contact Us</h2>
                <p>Do you have any queries? We’re happy to help!</p>
                <p>We love hearing our customers and their ideas to translate them into digital realities.
                Our team is available to solve all your doubts and offer you a wide range of IT services
                that help you take your business to the next level.</p>
                <p><b>Don't forget to Claim your Free Quotation!</b></p>
            </div>
        </div>
    )
}
function AboutUs() {
    return (
        <Container className='my-5'>
            <Media>
                <Media.Body>
                    <h3>Our Vision</h3>
                    <p>
                        Our Vision is to be a well-established web and mobile app development company to serve SMEs
                        and Enterprises. The team of The One Technologies has been providing innovative, cost-efficient
                        and timely solutions that generate the growth of small and medium-sized businesses globally.
                        We place a consistent innovation to bring about an ultimate crest of triumph as our service.
                        We believe and strongly strive to serve any business with honesty.
                    </p>
                </Media.Body>
                <img width={200} height={150} className="ml-3" src={vision} alt="Generic placeholder" />
            </Media>
        </Container>
    )
}
function Topics() {
    debugger
    const { path, url } = useRouteMatch()
    return (<>
        <ul>
            <li><Link to={`${url}/dot-net`}>Dot Net</Link></li>
            <li><Link to={`${url}/react`}>React</Link></li>
            <li><Link to={`${url}/mobile`}>Mobile Development</Link></li>
        </ul>
        <Switch>
            <Route path={`${path}/dot-net`}><DotNet /></Route>
            <Route path={`${path}/react`}><ReactJs /></Route>
            <Route path={`${path}/mobile`}><MobileDevelopment /></Route>
            <Route path={`${path}`}>Please choose the topic</Route>
            <Route path='*'>not found</Route>

        </Switch>
    </>)
}
function Topic() {
    debugger
    const { topicid } = useParams()
    return (<div>
        <h2>This is {topicid}</h2>
    </div>)
}
function DotNet() {
    const { path, url } = useRouteMatch()
    return (<>
        <ul>
            <li><Link to={`${url}/.net-core`}>.Net Core</Link></li>
            <li><Link to={`${url}/.net-mvc`}>.Net MVC</Link></li>
          
        </ul>
        <Switch>
            <Route path={`${path}/:topicid`}><Topic /></Route>
            <Route path={`${path}`}>Please choose the topic</Route>
                     <Route path='*'>not found</Route>
        </Switch>
    </>)
}
function ReactJs() {
    const { path, url } = useRouteMatch()
    return (<>
        <ul>
            <li><Link to={`${url}/React-js`}>React JS</Link></li>
            <li><Link to={`${url}/React-native`}>React Native</Link></li>
            <li><Link to={`${url}/React-vue`}>React Vue</Link></li>
        </ul>
        <Switch>
            <Route path={`${path}/:topicid`}><Topic /></Route>
            <Route path={`${path}`}>Please choose the topic</Route>
            <Route path='*'>not found</Route>
        </Switch>
    </>)
}
function MobileDevelopment() {
    const { path, url } = useRouteMatch()
    return (<>
        <ul>
            <li><Link to={`${url}/ios`}>IOS</Link></li>
            <li><Link to={`${url}/android`}>Android</Link></li>
            <li><Link to={`${url}/xamrine`}>Xmarine</Link></li>
        </ul>
        <Switch>
            <Route path={`${path}/:topicid`}><Topic /></Route>
            <Route path={`${path}`}>Please choose the topic</Route>
            <Route path='*'>not found</Route>
        </Switch>
    </>)
}
function Users() {
    const query = new URLSearchParams(useLocation().search)
    const QueryParams = query;
    let array = [];
    const html = QueryParams.forEach(function (value, key) {
    const result = { key : key, value : value };
    array.push(result);
    });
    
    
    const elements = array.map((result) => <h3> {result.key} : {result.value} </h3>)

    return(<div>
        {elements}
    </div>)
}
   

function Header() {
    return (<>
        <Router >
            <Navbar collapseOnSelect expand="md" bg="dark" variant="dark" sticky='top' >
                <Navbar.Brand><Link to='/'><img src={logo} width={55} height={55}></img></Link></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto p-3 "  >
                        <Nav.Link><Link to='/' className='text-warning' >Home</Link></Nav.Link>
                        <Nav.Link><Link to='/blogs' className='text-warning'>Blogs</Link></Nav.Link>
                        <Nav.Link><Link to='/services' className='text-warning'>Services</Link></Nav.Link>
                        <Nav.Link><Link to='/gallary' className='text-warning'>Gallary</Link></Nav.Link>
                        <Nav.Link><Link to='/technologies' className='text-warning'>Technologies</Link></Nav.Link>
                        <Nav.Link><Link to='/users' className='text-warning'>Users</Link></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>

            <Switch>
                <Route exact path='/'><Home /></Route>
                <Route path='/blogs'><Blogs /></Route>
                <Route path='/services'><Services /></Route>
                <Route path='/gallary'><Gallary /></Route>
                <Route path='/technologies'><Topics /></Route>
                <Route path='/about-us'><AboutUs /></Route>
                <Route path='/contact-us'><ContactUs /></Route>
                <Route path='/users'><Users /></Route>
                <Route path='*'>not found</Route>
            </Switch>
            <div className='footer '>
                <div className='d-flex justify-content-around'>
                    <ul type='none' className="p-2 mt-3">
                        <li className='mb-2'><Link to='/about-us'>About Us</Link></li>
                        <li><Link to='/contact-us'>Contact Us</Link></li>
                    </ul>
                    <div className='p-3'>
                        <h2 className='text-warning'>Feedback</h2>
                        <input className='rounded mr-2' type='text' placeholder='Email'></input>
                        <input className='rounded' type='text' placeholder='Feedback'></input>
                        <button className='btn-success rounded ml-2'>Submit</button>
                    </div>
                </div>
            </div>
        </Router>
    </>)
}

export default Header